/* 封装搜索组件 */
<template>
  <div>
    <div class="div">
      <a href="javascript:history.back(-1)">
        <img class="img" src="../../assets/static/backblack.png" />
      </a>
      <van-search
        class="search"
        v-model="value"
        show-action
        placeholder="请输入搜索关键词"
        @search="onSearch"
        @cancel="onCancel"
      />
    </div>
  </div>
</template>
<script>
import { Toast } from "vant";
export default {
  name: "Search",
  components: {},
  data() {
    return {
      value: "",
      backurl: "assets/static/back.png"
    };
  },
  methods: {
    onSearch(val) {
      Toast(val);
    },
    onCancel() {
      Toast("取消");
    }
  }
};
</script>
<style lang="scss" scoped>
.div {
  margin: 0 auto;
  padding: 0 16px;
  .img {
    position: absolute;
    top: 17px;
    left: 5%x;
    display: inline-block;
  }
  .search {
    width: 95%;
    position: relative;
    right: -5%;
  }
}

